import React from 'react';
import {Link} from 'react-router-dom';

import '../css/Login.css';
import axios from "axios";

class Login extends React.Component {

    state = {
        peoples: [],
        usuario: '',
        senha: ''
    }

    constructor(props) {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    // Pr√©-carregamento de dados.
    // Gambiarra para n√£o precisar usar a fun√ß√£o await do JS kk
    componentDidMount() {
        axios.get(`http://localhost:8000/usuario`)
            .then(res => {
                console.log(res.data)
                const peoples = res.data;
                this.setState({peoples});
            })
    }

    // Atualiza valores das vari√°veis conforme altera√ß√£o nos campos
    handleChange = event => {
        this.setState({
            [event.target.name]: event.target.value
        });
    }

    // Fun√ß√£o requisita dados atualizados do bd
    // Verifica se a conta est√° armazenada no bd ou n√£o
    // e exibe um alert ao usu√°rio
    handleSubmit(event) {
        event.preventDefault();

        // Faz requisi√ß√£o de usuarios cadastrados
        axios.get(`http://localhost:8000/usuario`)
            .then(res => {
                console.log(res.data)
                const peoples = res.data;
                this.setState({peoples});
            })

        // Compara se o usu√°rio inserido faz parte do banco de dados
        let comp = false;
        this.state.peoples.forEach(people => {
            if ((people.usuario == this.state.usuario) && people.senha == this.state.senha)
                comp = true;
        })

        // Exibe um alert, conforme sa√≠da do if acima
        if (comp)
            alert('Bem vindo!')
        else
            alert('Usu√°rio n√£o encontrado!')

    }

    render() {
        return (
            <div className="Login">
                <div className="wrapper fadeInDown">
                    <div id="formContent">
                        { /* Formulario Login */}
                        <form onSubmit={this.handleSubmit}>
                            <h1>Login</h1>
                            <input type="text" id="usuario" onChange={this.handleChange} className="fadeIn second"
                                   name="usuario"
                                   placeholder="Usu√°rio"/>
                            <input type="text" id="senha" onChange={this.handleChange} className="fadeIn third"
                                   name="senha" placeholder="Senha"/>
                            <input type="submit" className="fadeIn fourth" value="Entrar"/>
                        </form>

                        { /* Criar nova conta */}
                        <div id="formFooter">
                            <Link className="underlineHover" to="cadastro">Criar nova conta</Link>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Login